.wrapper {
    padding: 24px;
    display: block;
    width: calc(100% - 550px);

    .top-side {
        .first {
            border-bottom: 1px solid #393C49;

            h1 {
                font-weight: 600;
                font-size: 28px;
                color: #FFFFFF;
            }

            p {
                font-size: 16px;
                color: #ABBBC2;
                margin-top: 5px;
                margin-bottom: 24px;
            }
        }

        .second {
            margin-top: 24px;

            ul {
                display: flex;
                flex-wrap: wrap;
                list-style: none;

                li {
                    width: 200px;
                    height: 145px;
                    padding: 16px;
                    margin-right: 24px;
                    background: #1F1D2B;
                    border-radius: 8px;

                    .top-side {
                        display: flex;
                        align-items: center;

                        .logo-wrapper {
                            width: 38px;
                            height: 38px;
                            display: flex;
                            justify-content: center;
                            border-radius: 8px;
                            align-items: center;
                            background: #252836;
                        }

                        .increase {
                            font-size: 12px;
                            color: #50D1AA;
                            margin-left: 12px;
                            margin-right: 6px;
                        }

                        .decrease {
                            font-size: 12px;
                            color: #FF7CA3;
                            margin-left: 12px;
                            margin-right: 6px;
                        }
                    }

                    h3 {
                        font-size: 28px;
                        color: #FFFFFF;
                        margin: 8px 0;
                    }

                    p {
                        font-size: 14px;
                        color: #ABBBC2;
                    }
                }
            }
        }
    }

    .order-table {
        margin-top: 24px;
        background: #1F1D2B;
        border-radius: 8px;
        height: calc(100vh - 380px);

        &-top {
            padding: 24px;
            border-bottom: 1px solid #ABBBC2;
            .line {
                margin-top: 16px;
                display: flex;
                justify-content: space-between;

                h2 {
                    font-size: 20px;
                    color: #FFFFFF;
                }

                button {
                    background: #1F1D2B;
                    border: 1px solid #393C49;
                    border-radius: 8px;
                    font-size: 14px;
                    color: #FFFFFF;
                    padding: 14px;
                    position: relative;
                    padding-left: 44px;
                    outline: none;

                    &::before {
                        position: absolute;
                        content: " ";
                        background: url(./images/filterIcon.svg);
                        width: 17px;
                        height: 16px;
                        background-size: cover;
                        left: 16px;
                        top: 0;
                        bottom: 0;
                        margin: auto 0;
                    }
                }

                h3 {
                    font-size: 14px;
                    font-weight: 550;
                    color: #FFFFFF;
                }
            }
        }
        &-bottom{
            height: calc(100%  - 150px);
            overflow-y: auto;
            padding: 24px;
        }
    }
}