.meal-card {
    width: 221px;
    height: 299px;
    border: 1px solid #393C49;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: space-between;
    overflow: hidden;
    margin-bottom: 24px;
    .meal-img {
        border-radius: 50%;
        margin-top: 22px;
        margin-bottom: 14px;
    }

    h3 {
        width: 144px;
        font-size: 14px;
        color: #FFFFFF;
        font-weight: lighter;
    }

    .span-wrapper {
        display: flex;
        width: 112px;
        justify-content: space-between;

        span {
            font-size: 14px;
            color: #ABBBC2;
            font-weight: lighter;
        }
    }

    .card-bottom {
        width: 100%;
        padding: 16px 0;
        background: #ea7c698e;
        display: flex;
        justify-content: center;
        align-items: center;
        p {
            font-size: 14px;
            font-weight: bold;
            color: #EA7C69;
            margin-left: 10px;
        }
    }
}