.payment-sidebar {
    position: absolute;
    right: 0;
    top: 0;
    background: #1F1D2B;
    padding: 24px;
    z-index: 5;
    transition: all ease 0.5s;
    color: white;
    height: 100%;
    padding: 0;
    width: 0;
    right: 0;
    overflow: hidden;

    &.active {
        right: 0;
        width: 405px;
        overflow-y: auto;
        padding: 24px;
        padding-top: 64px;
    }

    .top-payment {
        h2 {
            font-weight: 600;
            font-size: 28px;
        }

        p {
            font-weight: 500;
            font-size: 16px;
            color: #ABBBC2;
            margin-top: 8px;
        }
    }

    .payment-settings {
        padding: 24px 0;
        margin: 24px 0;
        border-top: 1px solid #393C49;
        border-bottom: 1px solid #393C49;

        h2 {
            font-size: 20px;
            color: #FFFFFF;
        }

        .button-wrapper {
            width: 357px;
            display: flex;
            margin-top: 22px;
            margin-bottom: 16px;

            .type-payment {
                width: 101px;
                height: 64px;
                background: #252836;
                border: 1px solid #ABBBC2;
                border-radius: 8px;
                outline: none;
                margin-right: 8px;
                display: flex;
                text-align: center;
                justify-content: center;
                align-items: center;
                position: relative;
                flex-direction: column;
                font-weight: 500;
                font-size: 14px;
                line-height: 130%;
                color: #ABBBC2;
                transition: all ease 0.5s;

                &.active {
                    border: 1px solid #FFFFFF;
                    color: #FFFFFF;

                    .icon {
                        fill: white;
                    }

                    .checked-icon {
                        opacity: 1;
                    }
                }

                .checked-icon {
                    transition: all ease 0.5s;
                    position: absolute;
                    top: 6px;
                    right: 6px;
                    opacity: 0;
                }

                .icon {
                    margin-bottom: 6px;
                    fill: #ABBBC2;
                }
            }
        }

        .line {
            display: flex;
            flex-direction: column;
            margin-bottom: 16px;
            width: 100%;

            label {
                font-weight: 500;
                font-size: 14px;
                color: #FFFFFF;
                margin-bottom: 8px;
            }

            input {
                padding: 14px;
                outline: none;
                background: #2D303E;
                border: 1px solid #393C49;
                border-radius: 8px;
                color: #ABBBC2;
            }

            &:last-of-type {
                display: flex;
                flex-wrap: wrap;
                flex-direction: row;
                justify-content: space-between;

                .left,
                .right {
                    width: 45%;

                    input {
                        width: 100%;
                    }
                }
            }
        }
    }

    .line-bottom {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;

        .left,
        .right {
            width: 45%;
            flex-direction: column;

            label {
                display: block;
                font-weight: 500;
                font-size: 14px;
                color: #FFFFFF;
                margin-bottom: 8px;
            }

            input {
                background: #2D303E;
                border: 1px solid #393C49;
                border-radius: 8px;
                padding: 14px;
                outline: none;
                width: 100%;
                color: white;
            }

            select {
                background: #1F1D2B;
                width: 100%;
                padding: 14px;
                color: white;
                border: 1px solid #393C49;
                border-radius: 8px;
                outline: none;
            }
        }
    }

    .conifrom-line {
        margin-top: 50px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        .cancel {
            width: 47%;
            padding: 14px 0;
            border: 1px solid #EA7C69;
            border-radius: 8px;
            background: transparent;
            color: #EA7C69;
            font-weight: 100;
            font-size: 14px;
            outline: none;
            transition: all ease 0.5s;

            &:hover {
                background: #EA7C69;
                color: white;
            }
        }

        .submit-payment {
            width: 47%;
            padding: 14px 0;
            border: 1px solid #EA7C69;
            border-radius: 8px;
            color: white;
            font-weight: 100;
            outline: none;
            font-size: 14px;
            background: #EA7C69;
            transition: all ease 0.5s;

            &:hover {
                background: transparent;
                color: #EA7C69;
            }
        }
    }
}

.shape {
    position: fixed;
    top: 0;
    width: 0;
    right: 0;
    height: 100%;
    z-index: 4;
    transition: all ease 0.5s;
    background: #09090db6;

    &.active {
        width: 100%;
    }
}