.home_page {
    width: calc(100% - 105px - 408px);
    padding: 24px;

    .top-side {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .left {
            h1 {
                font-weight: 600;
                font-size: 28px;
                line-height: 140%;
                color: #FFFFFF;
            }

            p {
                font-size: 16px;
                color: #E0E6E9;
            }
        }

        .right {
            position: relative;

            .search-input {
                background: #2D303E;
                border: 1px solid #393C49;
                box-sizing: border-box;
                border-radius: 8px;
                position: relative;
                padding: 14px 14px 14px 42px;
                color: #ccc;
                outline: none;
            }

            img {
                z-index: 2;
                position: absolute;
                left: 14px;
                width: 20px;
                height: 20px;
                top: 0;
                bottom: 0;
                margin: auto 0;
            }
        }
    }

    .type_dishes {
        margin-top: 24px;
        width: 100%;

        .button_group {
            display: flex;
            margin-bottom: 12px;

            .button {
                transition: all ease 0.5s;
                font-weight: 600;
                font-size: 14px;
                line-height: 140%;
                color: #FFFFFF;
                background: transparent;
                border: none;
                margin-right: 32px;
                outline: none;

                &.active {
                    color: #EA7C69;
                }
            }
        }

        .line_group {
            width: 100%;
            position: relative;

            .line {
                width: 100%;
                display: block;
                height: 2px;
                background: #393C49;
            }

            .first_line {
                width: 100%;
                width: 40px;
                display: block;
                background: #EA7C69;
                height: 3px;
                position: absolute;
                left: 0;
                bottom: 0;
            }
        }
    }

    .dish-menu {
        margin-top: 24px;

        .top-menu {
            display: flex;
            justify-content: space-between;
            margin-bottom: 12px;

            h2 {
                font-size: 20px;
                color: #FFFFFF;
            }

            select {
                padding: 14px;
                background: transparent;
                color: white;
                border: 1px solid #393C49;
                border-radius: 8px;
                outline: none;

                option {
                    background: #393C49;
                    color: white;
                }
            }
        }

        .card-wrapper {
            height: calc(100vh - 250px);
            display: flex;
            flex-wrap: wrap;
            overflow-y: auto;
            justify-content: space-between;

            .card {
                margin: 50px 0;
                min-height: 226px;
            }
        }
    }
}