.sidebar-right {
    width: 400px;
    padding: 24px;

    .top-part {
        background: #1F1D2B;
        border-radius: 8px;
        padding: 24px;

        .line {
            display: flex;
            justify-content: space-between;
            align-items: center;

            h2 {
                font-size: 20px;
                color: #FFFFFF;
                font-weight: 100;
            }

            select {
                background: #1F1D2B;
                border: 1px solid #393C49;
                border-radius: 8px;
                padding: 12px 14px;
                outline: none;
                color: white;
            }
        }

        .order-wrapper {
            margin: 12px 0;
            border-top: 1px solid #ABBBC2;
            padding: 12px 0;

            .order-line {
                display: flex;
                align-items: center;
                margin: 12px 0;

                img {
                    width: 55px;
                    height: 55px;
                    border-radius: 50%;
                    margin-right: 14px;
                }

                .left {
                    p {
                        font-size: 14px;
                        color: #E0E6E9;
                    }

                    span {
                        font-size: 12px;
                        color: #ABBBC2;
                    }
                }
            }
        }

        .view-all {
            width: 100%;
            padding: 14px 0;
            border: 1px solid #EA7C69;
            border-radius: 8px;
            outline: none;
            background: transparent;
            font-size: 14px;
            line-height: 140%;
            color: #EA7C69;
            transition: all ease 0.5s;
            font-weight: bold;

            &:hover {
                background: #EA7C69;
                color: white;
            }
        }
    }

    .bottom-part {
        margin-top: 24px;
        padding: 24px;
        background: #1F1D2B;
        border-radius: 8px;

        .top-side {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 24px 0;
            border-bottom: 1px solid #393C49;
            margin-bottom: 20px;

            h2 {
                font-size: 20px;
                color: #FFFFFF;
            }

            button {
                outline: none;
                padding: 14px;
                padding-left: 44px;
                border: 1px solid #393C49;
                background: transparent;
                border-radius: 8px;
                font-size: 14px;
                color: #FFFFFF;
                position: relative;
                &::before{
                    position: absolute;
                    content: " ";
                    background: url(../DashboradPage/images/arrow-ios-down.svg);
                    left: 16px;
                    width: 20px;
                    height: 20px;
                    top: 0;
                    bottom: 0;
                    margin: auto 0;
                }
            }
        }
    }
}