.order {
    padding: 24px;
    background: #1F1D2B;
    color: white;
    min-height: 100vh;

    .subtitle-otrder {
        font-weight: 600;
        font-size: 20px;
    }

    .top_button-group {
        display: flex;
        flex: wrap;
        margin: 24px 0;

        .button_top {
            margin-right: 8px;
            padding: 7px 12px;
            border-radius: 8px;
            background: transparent;
            outline: none;
            border: 1px solid #393C49;
            color: #EA7C69;
            font-weight: 600;
            font-size: 14px;
            transition: all ease 0.5s;

            &.active {
                background: #EA7C69;
                color: white;
            }
        }
    }

    .top_line {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        span {
            display: flex;

            p:first-of-type {
                margin-right: 43px;
            }
        }
    }

    .order-item-wrapper {
        display: flex;
        border-top: 1px solid #393C49;
        border-bottom: 1px solid #393C49;
        width: 360px;
        padding: 24px 0;
        height: 50vh;
        margin: 24px 0;
        flex-direction: column;
        overflow-y: auto;
        overflow-x: hidden;

        .order-item {
            display: flex;
            width: 100%;
            justify-content: space-between;
            margin: 12px 0;

            .left {
                width: 80%;

                .top {
                    display: flex;
                    flex-wrap: wrap;
                    align-items: center;
                    justify-content: space-between;

                    .wrapper {
                        display: flex;

                        .about-meal {
                            p {
                                font-weight: 500;
                                font-size: 14px;
                                width: 150px;
                            }

                            span {
                                color: #ABBBC2;
                                font-weight: 500;
                                font-size: 12px;
                            }
                        }

                    }

                    .number-meal {
                        width: 48px;
                        height: 48px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        border-radius: 8px;
                        outline: none;
                        background: #2D303E;
                        color: white;
                        border: 1px solid #393C49;
                    }

                    img {
                        width: 50px;
                        border-radius: 50%;
                        margin-right: 5px;
                        height: 50px;
                    }
                }

                .bottom {
                    margin-top: 10px;

                    input {
                        width: 100%;
                        padding: 14px;
                        color: white;
                        border-radius: 8px;
                        outline: none;
                        border: 1px solid #393C49;
                        background: #2D303E;
                        opacity: 0.8;
                    }
                }
            }

            .right {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;

                span {
                    margin-bottom: 23px;
                }

                .delete-button {
                    width: 48px;
                    height: 48px;
                    border: 1px solid #FF7CA3;
                    border-radius: 8px;
                    background: transparent;
                    outline: none;
                }
            }
        }
    }

    .bottom_order {
        width: 100%;

        .line {
            display: flex;
            justify-content: space-between;
            margin-bottom: 18px;

            p {
                font-size: 14px;
                color: #ABBBC2;
            }

            span {
                font-weight: 500;
                font-size: 16px;
                color: #FFFFFF;
            }

        }
    }

    .submit-btn {
        background: #EA7C69;
        box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.3);
        border-radius: 8px;
        border: none;
        outline: none;
        color: white;
        width: 100%;
        padding: 14px 0;
        margin-top: 45px;
        font-weight: 600;
        font-size: 14px;
    }
}