*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.container{
    display: flex;
    flex-wrap: wrap;
}

.wrapper{
  h1{
    color: white;
  }
}

body{
    background: #252836;
    font-family: 'Barlow', sans-serif;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px transparent;
}

::-webkit-scrollbar-thumb {
  background: #EA7C69;
  border-radius: 50px;
}

.dashboard_container{
  width: calc(100% - 105px);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}