.order-card {
    display: flex;
    justify-content: space-between;
    padding: 10px 0;

    .left {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 65%;
        font-size: 14px;
        color: #E0E6E9;

        .name {
            display: flex;
            align-items: center;
            min-width: 160px;

            span {
                margin-left: 16px;
            }
        }

        .meal {
            width: 150px;
        }
    }

    .right {
        .completed {
            padding: 4px 12px;
            background: rgba(107, 226, 190, 0.24);
            border-radius: 30px;
            font-size: 14px;
            color: #50D1AA;
        }
        .preparing{
            padding: 4px 12px;
            background: rgba(146, 144, 254, 0.2);
            border-radius: 30px;
            font-size: 14px;
            color: #9290FE;
        }
        .pending{
            padding: 4px 12px;
            background: rgba(255, 181, 114, 0.2);
            border-radius: 30px;
            font-size: 14px;
            color: #FFB572;
        }
    }
}