.card {
    background: #1F1D2B;
    width: 192px;
    position: relative;
    padding: 114px 24px 24px 24px;
    text-align: center;
    border-radius: 16px;
    
    .img-wrapper {
        img {
            height: 132px;
            width: 132px;
            border-radius: 50%;
        }

        position: absolute;
        top: -36px;
    }

    h2 {
        font-weight: lighter;
        font-size: 14px;
        line-height: 130%;
        color: #FFFFFF;
    }
    p{
        font-size: 14px;
        color: #FFFFFF;
        margin: 8px 0;
    }
    span{
        font-size: 14px;
        color: #ABBBC2;
    }
}