.sidebar {
    background: #1F1D2B;
    display: flex;
    flex-direction: column;
    padding-left: 12px;
    min-height: 100vh;
    border-radius: 16px 16px 0 0;

    .icon {
        &.active {
            &::before {
                background: #252836;
            }

            &:after {
                background: #252836;
            }

            background: #252836;

            svg {
                box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.32);
                background: #EA7C69;
                fill: white
            }
        }

        &:first-of-type {
            &:hover {
                background: transparent;

                svg {
                    background: transparent;
                    box-shadow: 0 0 0 0 transparent;
                    fill: none;
                }
            }

            svg {
                padding: 0;
            }
        }

        border-radius: 12px 0 0 12px;
        transition: all ease 0.5s;
        padding: 13px 24px 11px 12px;
        position: relative;

        &::before {
            content: ' ';
            clip-path: polygon(33% 17%, 0 0, 100% 0, 100% 100%, 89% 70%, 78% 54%, 65% 39%, 53% 30%);
            width: 12px;
            height: 12px;
            right: 0;
            background: transparent;
            bottom: -12px;
            position: absolute;
        }

        &::after {
            transform: rotate(90deg);
            content: ' ';
            clip-path: polygon(33% 17%, 0 0, 100% 0, 100% 100%, 89% 70%, 78% 54%, 65% 39%, 53% 30%);
            width: 12px;
            height: 12px;
            right: 0;
            background: transparent;
            top: -12px;
            position: absolute;
        }

        svg {
            transition: all ease 0.5s;
            padding: 18px;
            box-sizing: content-box;
            fill: #EA7C69;
            border-radius: 8px;
        }

        &:hover {
            &::before {
                background: #252836;
            }

            &:after {
                background: #252836;
            }

            background: #252836;

            svg {
                box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.32);
                background: #EA7C69;
                fill: white
            }
        }
    }
}